import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';
import linkState from 'linkstate';

import { route } from 'preact-router';

import mu from '../../lib/musher-util';

const MARGIN_TOP_BACK = mu.marginTopBack(false);

const initialState = {
	searchText: '',
    showInfo: {},
};

@withText({
    veterinaryAllowed: <Text id='users.settings-allow-racevet'>Tillat veterinær</Text>,
    veterinaryAllowedHelp: <Text id='users.settings-veterinary-help'>Dersom du ønsker at godkjente veterinærer skal kunne søke deg opp og legge inn vaksiner må du skru på her.</Text>,
    imperial: <Text id='users.settings-imperial'>Imperial units</Text>,
    imperialHelp: <Text id='users.settings-imperial-email'>Use imperial units like mils, mph, Fahrenheit.</Text>,
})
@observer
class Settings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    toggleDarkmode = (e) => {
        const { appState } = this.props.stores;
		appState.toggleDarkmode();
	}

    toggleInfo = (e) => {
        const { name } = e.target.closest('i').dataset;
        const { showInfo } = this.state;
        showInfo[name] = !showInfo[name];
        this.setState({ showInfo });
	}

    toggleSetting= async (field) => {
        const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;
        await userStore.updateSetting({ [`settings.${field}`]: settings[field] === 1 ? 0 : 1 }, { [field]: settings[field] === 1 ? 0 : 1 });
    }

	componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.search !== this.props.search) {
            this.loadAll(nextProps);
        }
    }

    reload = (e) => {
        e.preventDefault();
        e.stopPropagation();
        window.location.reload();
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	render() {
		const { submenu, hits, searchText, showInfo = {} } = this.state;
		const { appState, userStore } = this.props.stores;
		const { search } = this.props;

		const { darkmode, viewmode } = appState;

		const { isAdmin, user } = userStore;
        const { settings = {}, language = 'en' } = user;

		const { searchResultStories, searchResultDogs, searchResultWorkouts, searchResultTeams, searchResultTracks, searchResultUsers } = appState;
		return (
			<div class='container-fluid' style='margin-bottom: 200px; margin-top: 60px;'>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>

				<div class='row m-2'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<h1><Text id='settings.title'>Settings</Text></h1>
                    </div>
                </div>
				<div class='row m-2'>
					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-0 mb-2'>
						<div class='d-flex justify-content-center py-3'>
							<Localizer>
								<input class='form-control form-control mr-2' type='search' placeholder={<Text id='settings.search-placeholder'>Search for settings...</Text>}
									value={searchText}
									onInput={linkState(this, 'searchText')}
								/>
							</Localizer>
						</div>
					</div> */}

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2'>
                        <h5 class='m-0 text-muted'><Text id='settings.quick-settings'>Quick settings</Text></h5>
                    </div>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3 bg-light rounded-lg' style='font-size: 1.2em;'>
						<div class='row'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-secondary py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-moon text-warning' />
                                </button>
                                <span class='flex-grow-1' style='line-height: 1.0em;'>
                                    Darkmode
                                    <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={'darkmode'} />
                                    {showInfo.darkmode && <>
                                        <br /><small class='text-muted'><Text id='settings.darkmode'>Dark or light layout</Text></small>
                                    </>}
                                </span>
                                <div class='custom-control custom-switch custom-switch-lg mt-2' style="transform: scale(1.8);">
                                    <input type='checkbox' class='custom-control-input' id='settingsDarkmode' onInput={() => this.toggleDarkmode()} checked={darkmode} />
                                    <label class='custom-control-label' for='settingsDarkmode' />
                                </div>
							</div>
						</div>
                        {['imperial'].map((setting, idx) => <>
                            <div class={`row ${idx >= 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1' style='line-height: 1.0em;'>
                                        {this.props[setting]}
                                        {this.props[`${setting}Help`] && <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={setting} />}
                                        {this.props[`${setting}Help`] && showInfo[setting] && <>
                                            <br /><small class='text-muted'>{this.props[`${setting}Help`]}</small>
                                        </>}
                                    </span>
                                    <div class='custom-control custom-switch custom-switch-lg mt-2 ml-3' style="transform: scale(1.8);">
                                        <input type='checkbox' class='custom-control-input' id={`settings${setting}`} onInput={() => this.toggleSetting(setting)} checked={settings[setting]} />
                                        <label class='custom-control-label' for={`settings${setting}`} />
                                    </div>
                                </div>
                            </div>
                        </>)}
					</div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3 mb-2'>
                        <h5 class='m-0 text-muted'><Text id='settings.veterinary'>Veterinary</Text></h5>
                    </div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-3 bg-light rounded-lg' style='font-size: 1.2em;'>
                        {['veterinaryAllowed'].map((setting, idx) => <>
                            <div class={`row ${idx > 0 ? 'border-top' : ''}`}>
                                <div class='col-12 py-2 d-flex align-items-center position-relative'>
                                    <span class='flex-grow-1' style='line-height: 1.0em;'>
                                        {this.props[setting]}
                                        {this.props[`${setting}Help`] && <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={setting} />}
                                        {this.props[`${setting}Help`] && showInfo[setting] && <>
                                            <br /><small class='text-muted'>{this.props[`${setting}Help`]}</small>
                                        </>}
                                    </span>
                                    <div class='custom-control custom-switch custom-switch-lg mt-2 ml-3' style="transform: scale(1.8);">
                                        <input type='checkbox' class='custom-control-input' id={`settings${setting}`} onInput={() => this.toggleSetting(setting)} checked={settings[setting]} />
                                        <label class='custom-control-label' for={`settings${setting}`} />
                                    </div>
                                </div>
                            </div>
                        </>)}
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3 bg-light rounded-lg' style='font-size: 1.2em;'>
						<div class='row'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-secondary py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-cog' />
                                </button>
                                <span class='flex-grow-1' style='line-height: 1.0em;'>
                                    <a href='/settings/general' class='stretched-link'><Text id='settings.general'>General</Text></a>
                                    <i class='fas fa-info-circle text-muted ml-2' onClick={this.toggleInfo} data-name={'general'} />
                                    {showInfo.general && <>
                                        <br /><small class='text-muted'><Text id='settings.general-info'>Language, viewmode, logout</Text></small>
                                    </>}
                                </span>
                                <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                    <i class='fas fa-angle-right' />
                                </button>
							</div>
						</div>
						<div class='row border-top'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-primary py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-user' />
                                </button>
                                <span class='flex-grow-1'>
                                    <a href='/settings/user' class='stretched-link'><Text id='settings.user'>User</Text></a>
                                </span>
                                <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                    <i class='fas fa-angle-right' />
                                </button>
							</div>
						</div>
						<div class='row border-top'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-info py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-users' />
                                </button>
                                <span class='flex-grow-1'>
                                    <a href='/settings/team' class='stretched-link'><Text id='settings.team'>Team</Text></a>
                                </span>
                                <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                    <i class='fas fa-angle-right' />
                                </button>
							</div>
						</div>
					</div>

					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3 bg-light rounded-lg'>
						<div class='row'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-secondary py-0 px-1 mr-3' style='width: 2.0em; background-color: #fc4c02; border-color: #fc4c02;'>
                                    <i class='fab fa-strava' />
                                </button>
                                <span class='flex-grow-1'>
                                    <a href='/users/integrations' class='stretched-link'><Text id='settings.strava'>Strava</Text></a>
                                </span>
                                <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                    <i class='fas fa-angle-right' />
                                </button>
							</div>
						</div>
						<div class='row border-top'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-success py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-plug' />
                                </button>
                                <span class='flex-grow-1'>
                                    <a href='/users/integrations' class='stretched-link'><Text id='settings.garmin-connect'>Garmin Connect</Text></a>
                                </span>
                                <button class='btn btn-link btn-lg py-0 px-1 mr-3'>
                                    <i class='fas fa-angle-right' />
                                </button>
							</div>
						</div>
					</div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3 bg-light rounded-lg'>
						<div class='row'>
							<div class='col-12 py-2 d-flex align-items-center position-relative'>
                                <button class='btn btn-secondary py-0 px-1 mr-3' style='width: 2.0em;'>
                                    <i class='fas fa-sync' />
                                </button>
                                <span class='flex-grow-1'>
                                    <a href='#' class='stretched-link' onClick={this.reload}><Text id='settings.reload'>Reload app / Get latest version</Text></a>
                                </span>
							</div>
						</div>
                    </div>

				</div>
			</div>
		);
	}
}

export default Settings;
